@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  margin: 0;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: beige;
}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
}

@media screen and (max-aspect-ratio: 4/5) {
  #root {
    max-height: calc(90vh - 70px);
  }
}

.centered {
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
}

.grey {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 25%;
}

.number {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.head {
  font-size: 3em;
}

.setup {
  visibility: hidden;
}

.play {
  visibility: hidden;
}

.endgame {
  visibility: hidden;
}

.invisible {
  opacity: 0;
}

.visible {
  opacity: 1;
  position: absolute;
  width: 100vw;
  height: 100vh;
}

.selected {
  border-radius: 20px;
  background: white;
}

.tip {
  width: 100%;
  font-size: 0.75em;
}

.App {
  background-color: beige;
  height: 100vh;
  max-height: 100vh;
  font-family: 'Indie Flower', cursive;
  font-size: calc(3vh);
  display: grid;
}

@media screen and (max-aspect-ratio: 4/5) {
  .App {
    max-height: calc(90vh - 70px);
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    font-size: calc(3vw);
    flex-direction: column-reverse;
  }

  .Backwards {
    grid-row: 1;
    grid-column: 1;
    flex-direction: column-reverse;
  }

  .Forwards {
    grid-row: 1;
    grid-column: 2;
    flex-direction: column-reverse;
  }

  .Hazards {
    padding-bottom: 1em;
  }

  .Card {
    grid-row-start: 1;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 3;
  }

  .Yeah {
    grid-column: 2;
    grid-row-start: 1;
    grid-row-end: 4;
  }

  .Nah {
    grid-column: 1;
    grid-row-start: 1;
    grid-row-end: 4;
  }

  #showScore {
    z-index: 4;
    visibility: visible;
    position: absolute;
    width: 6em;
    height: 6em;
    padding: 0;
    margin: 0;
    bottom: calc(10vh + 70px);
    left: 0;
  }
  
  #showHistory {
    z-index: 4;
    visibility: visible;
    position: absolute;
    width: 6em;
    height: 6em;
    padding: 0;
    margin: 0;
    bottom: calc(10vh + 70px);
    right: 0;
  }

  .mobileHide {
    display: none;
  }

  .head {
    font-size: 10vw;
  }
}

@media screen and (min-aspect-ratio: 4/5) {
  .App {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
  }

  .Backwards {
    grid-row: 1;
    grid-column: 1;
    flex-direction: column;
  }

  .Forwards {
    grid-row: 1;
    grid-column: 4;
    flex-direction: column;
  }

  .Card {
    grid-row: 1;
    grid-column-start: 2;
    grid-column-end: 4;
    min-width: vh;
  }

  .Yeah {
    grid-row: 1;
    grid-column-start: 3;
    grid-column-end: 5;
  }

  .Nah {
    grid-row: 1;
    grid-column-start: 1;
    grid-column-end: 3;
  }

  #showScore {
    display: none;
  }
  
  #showHistory {
    display: none;
  }
}
.Landing {
    z-index: 5;
    color: midnightblue;
    grid-row-start: 1;
    grid-row-end: 200;
    grid-column-start: 1;
    grid-column-end: 100;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

h1 {
    font-size: 4em;
    margin: 0;
    padding: 0;
}

input {
    margin: 0.5em;
    padding: 0.5em;
    border-radius: 5vw 15vw 5vw 15vw/15vw 5vw 15vw 5vw;
    border-style: none;
    background: lightgrey;
    color: midnightblue;
    text-align: center;
    font-size: 1em;
    width: 10em;
}

#lobbyMessage {
    font-size: 2em;
    padding: 0.5em;
}

.lobbyButton {
    cursor: pointer;
    font-size: 1.5em;
    width: 5em;
    margin:  0.25em;
    padding: 0.25em 0em;
    border-radius: 5vw 15vw 5vw 15vw/15vw 5vw 15vw 5vw;
}

.hidden {
    display: none;
}

#joinGame {
    background-color: forestgreen;
}

#enterGame {
    background-color: gold;
}

#startGame {
    background-color: gold;
}

@media screen and (max-aspect-ratio: 4/5) {
    input {
        font-size: 1.5em;
    }
}
.Player {
    color: midnightblue;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    font-weight: bold;
}

.meeple {
    min-height: 1.5em;
    min-width: 1.5em;
    max-width: 10em;
    margin: 0.5em;
    margin-left: 0;
    font-size: 0.75em;
}

@media screen and (max-aspect-ratio: 4/5) {
    .Player {
        font-size: 1.25em;
    }
    
    .meeple {
        font-size: 1em;
    }
}
.Card {
    z-index: 1;
    background-image: linear-gradient(0deg, goldenrod, 95%, goldenrod, 95%, beige);
    color: midnightblue;
    margin: 1em;
    padding: 1em;
    border-radius: 0vw 0vw 10vw 10vw/0vh 0vh 90vh 90vh;
    border: 1em lightblue solid;
    border-top: none;
    text-align: center;
    display: grid;
    grid-template-rows: auto 3fr 1fr auto 1fr;
    grid-template-columns: 1fr 3fr 1fr;
}

.Title {
    grid-row: 3;
    grid-column: 2;
    font-size: 1.5em;
}

.FlavourText {
    font-size: 0.75em;
    grid-row: 4;
    grid-column-start: 2;
    grid-column-end: 3;
}

#score {
    font-size: 1em;
    grid-row: 5;
    grid-column: 2;
    justify-content: center;
    align-items: center;
}

#stats {
    grid-row: 1;
    grid-column-start: 1;
    grid-column-end: 4;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

#image {
    grid-row: 2;
    grid-column-start: 1;
    grid-column-end: 4;
    font-size: min(20vh, 10em);
}

@media screen and (max-aspect-ratio: 4/5) {
    .Card {
        margin-top: 5em;
        border-radius: 0vw 0vw 30vw 30vw/0vh 0vh 90vh 90vh;
    }

    .Title {
        font-size: 2em;
    }

    .FlavourText {
        font-size: 1em;
    }

    .Value {
        font-size: 1.5em;
    }

    #score {
        font-size: 2em;
    }
}
.Backwards {
    z-index: 2;
    color: midnightblue;
    min-width: 10vw;
    padding: 1em;
    margin: 1em;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
}

@media screen and (max-aspect-ratio: 4/5) {
    .Backwards {
        margin: 0;
        align-items: flex-start;
        justify-content: space-between;
    }
}
.Bonuses {
    color: midnightblue;
    width: 100%;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    flex-wrap: wrap;
}

.bonusSymbol {
    font-size: 1.5em;
}
.Forwards {
    z-index: 2;
    color: midnightblue;
    min-width: 10vw;
    padding: 1em;
    margin: 1em;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
}

.Turn {
    padding: 0.5em;
    margin: 0.5em;
    border-radius: 0.5em;
    text-align: left;
    font-size: 0.66em;
    background-color: lightblue;
    opacity: 0.90;
}

.historyList {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    max-height: 50vh;
    min-height: 50vh;
    overflow-y: hidden;
    border-radius: 0.5em;
    box-shadow: 
        inset 0px 5vh 28px -5vh lightgrey,
        inset 0px -5vh 28px -5vh lightgrey;
}

@media screen and (max-aspect-ratio: 4/5) {
    .Forwards {
        margin: 0;
        align-items: flex-end;
    }

    .historyList {
        visibility: hidden;
    }

    .playerList {
        display: flex;
    }
}
.Hazards {
    color: midnightblue;
    width: 100%;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    flex-wrap: wrap;
}

.hazardSymbol {
    font-size: 1.5em;
}
.Timer {
    z-index: 10;
    color: midnightblue;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
}
.Yeah {
    z-index: 3;
    background-color: forestgreen;
    font-size: 5em;
    text-align: center;
}

.Nah {
    z-index: 3;
    background-color: gold;
    font-size: 5em;
    text-align: center;
}
.Endgame {
    z-index: 4;
    color: midnightblue;
    background-color: beige;
    grid-row-start: 1;
    grid-row-end: 200;
    grid-column-start: 1;
    grid-column-end: 100;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}
